import PropTypes from "prop-types";
import { useState } from "react";
import { format } from "date-fns";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
} from "@mui/material";
// utils
import { fCurrency } from "../../../../utils/formatNumber";
// components
import Label from "../../../../components/Label";
import Image from "../../../../components/Image";
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
//

// ----------------------------------------------------------------------

SupervisorTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
};

export default function SupervisorTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
}) {
  const theme = useTheme();

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  const { t } = useTranslation();

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>

      <TableCell padding="checkbox" />

      <TableCell sx={{ display: "flex", alignItems: "center" }}>
        <Image
          disabledEffect
          alt={row.full_name}
          src={row.photo}
          sx={{ borderRadius: 1.5, width: 48, height: 48, mr: 2 }}
        />
        <Typography variant="subtitle2" noWrap>
          {row.full_name}
        </Typography>
      </TableCell>

      <TableCell align="center">{fCurrency(row.balance)}</TableCell>
      <TableCell align="center">{row.phone || "-"}</TableCell>
      <TableCell align="center">
        {t(`country.${row.country_name}`)}
      </TableCell>
      <TableCell align="center">{row.city}</TableCell>
      <TableCell align="center">{row.address}</TableCell>

      <TableCell align="center">
        <Label
          variant={theme.palette.mode === "light" ? "ghost" : "filled"}
          color={row.is_block ? "error" : "success"}
          sx={{ textTransform: "capitalize" }}
        >
          {row.is_block ? t("params.yes") : t("params.no")}
        </Label>
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              <MenuItem
                onClick={() => {
                  handleCloseMenu();
                  onDeleteRow();
                }}
                sx={{ color: "error.main" }}
              >
                <Iconify icon={"eva:trash-2-outline"} />
                {deleteText}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  onEditRow();
                  handleCloseMenu();
                }}
              >
                <Iconify icon={"eva:edit-fill"} />
                {editText}
              </MenuItem>
            </>
          }
        />
      </TableCell>
    </TableRow>
  );
}
